@import "../../../styles/common";
.intro-section{
.owl-carousel{
.owl-nav{
    position: absolute;
    top: 50%;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 40px;
    margin: 0;
    margin-top: -15px;
    color: #2b579a;
    font-size: 16px;
    line-height: 1;
    padding: 0;
    // border: 0.1rem solid #ebebeb;
    background: 0 0;
    cursor: pointer;
    border-radius: 50px;
    transition: all .3s ease;
    .owl-prev {
        left: 30px !important;
        opacity: 0;
        margin-left: 4rem;;
        margin-right: 20px;
        margin: 0;
        transform: translateY(-50%);
        font-size: 20px;
        font-weight: 400;
    }
    .owl-next {
        right: 30px !important;
        opacity: 0 !important;
        margin-right: 4rem !important;
        margin: 0 !important;
        transform: translateY(-50%) !important;
        font-size: 20px;
        font-weight: 400;
        
       
    }
}

}

}


